.wrapper {
	display: flex;
	align-items: center;
	padding: 5px;
	justify-content: space-between;
	margin-bottom: 5px;
	background: rgba(255, 255, 255, 0.05);
	border-radius: 6px;
	backdrop-filter: blur(15px);
	border-top: 1px solid rgba(255, 255, 255, 0.2);
	border-left: 1px solid rgba(255, 255, 255, 0.2);
	box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
}
.wrapper:last-child {
	border-bottom: none;
}
.coin_img {
	width: 50px;
	height: 50px;
	border-radius: 5px;
}
.symbol {
	display: inline-block;
	width: 45px;
}
.price {
	width: 70px;
	display: flex;
	align-items: flex-end;
	justify-content: center;
	flex-direction: column;
}
.price_change {
	font-size: 14px;
}
.price_change.red {
	color: rgb(246, 70, 93);
}
.price_change.green {
	color: #03a66d;
}
.dollar {
	color: #03a66d;
}
.ath {
	display: flex;
	flex-basis: 32%;
	align-items: flex-end;
	justify-content: center;
	flex-direction: column;
}
.ath_date {
	color: #fcd535;
	font-size: 14px;
}
@media screen and (max-width: 520px) {
	.symbol {
		font-size: 18px;
	}
	.coin_img {
		width: 40px;
		height: 40px;
	}
	.curr_ath {
		font-size: 14px;
	}
}
