body {
	background: rgb(11, 14, 17);
	background: linear-gradient(
		43deg,
		rgba(11, 14, 17, 1) 0%,
		rgba(24, 26, 32, 1) 100%
	);
	color: #eaecef;
	min-height: 100vh;
	padding: 10px;
	background-size: 400% 400%;
	animation: bg 5s ease-in infinite alternate-reverse;
}

@keyframes bg {
	0%,
	100% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100%, 50%;
	}
}

.App {
	min-width: 300px;
	max-width: 350px;
	margin: 0 auto;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
